module.exports = "<div id=create-mc-question-set> <div class=create-qcollection-modal v-show=!newQcollection.id transition=modal> <div class=modal-container> <h3 class=mdl-typography--headline style=\"margin:16px 0 8px 0;padding:16px 16px 0 16px\">第一步：創建題集</h3> <div style=\"padding:0 16px 16px 16px\" class=create-new-qcollection-form v-show=\"newQcollection.myQcollections.length === 0\"> <mdl-button primary style=margin-left:-16px @click=getMyQcollections()>或選擇現有題集</mdl-button> <div> <mdl-textfield floating-label=題集名字 :value.sync=newQcollection.name></mdl-textfield> </div> <mdl-select label=所屬學科 id=subject-select :value.sync=newQcollection.subject :options=subjects></mdl-select> <div style=\"padding:20px 0\"> <mdl-checkbox :checked.sync=newQcollection.public>公開</mdl-checkbox> </div> <div class=flex-row style=\"margin-bottom: 8px\"> <mdl-button style=margin-left:auto @click=goBack()>取消</mdl-button> <mdl-button primary raised style=margin-left:16px @click=createNewQcollection()>創建</mdl-button> </div> </div> <div class=my-qcollections v-show=\"newQcollection.myQcollections.length > 0\" style=\"max-height:400px; overflow:auto\"> <mdl-button primary style=padding-left:16px @click=\"newQcollection.myQcollections = []\">返回創建新題集</mdl-button> <div class=flex-column @click=\"createNewQcollection('from exist', qc)\" v-for=\"qc in newQcollection.myQcollections\" indexby=_id style=\"padding: 16px;border-bottom: 1px solid #eee;cursor:pointer\"> <div class=flex-row> <span style=\"background: #2196F3;color: #fff;padding: 0px 8px;border-radius: 12px;margin-right: 4px\">{{qc.subject | subject}}</span> <span>{{qc.name}}</span> </div> </div> <div class=\"flex-column flex-center\" style=\"margin:16px 0 32px 0\"> <mdl-button raised primary @click=nextPage() :disabled=!newQcollection.loadMore>加載更多 </mdl-button></div> </div> </div> </div> <mdl-button accent raised class=float-button @click=goToQCDetailView() v-bind:disabled=publishButton.disabled> 完成編輯 </mdl-button> <div class=body-wrapper> <div class=qcollection-inbox :class=\"{'show': qcollectionInbox.show}\"> <div class=\"qcollection-header flex-row flex-center\"> <span class=public>{{newQcollection.public | bTp}}</span> <span class=subject>{{newQcollection.subject | subject}}</span> <span class=name>{{newQcollection.name}}</span> <span class=\"flex-row flex-center\" style=margin-left:auto;font-size:18px;color:#2196F3><i class=material-icons style=font-size:24px;margin-right:8px>description</i> {{qcollectionInbox.questions.length}} 題</span> </div> <div class=\"flex-column questions\" v-show=qcollectionInbox.show> <div class=\"flex-row flex-baseline flex-no-shrink question\" v-for=\"q in qcollectionInbox.questions\" indexby=_id> <span style=margin-right:8px>{{$index + 1}}.</span> <span style=margin-right:8px>{{{q.context}}}</span> <span class=\"flex-row flex-center\" style=margin-left:auto;color:#FFC107>{{q.difficulty}}<i class=material-icons style=font-size:16px>star</i></span> </div> </div> <div class=expand-icon @click=toggleQCInbox()> <i class=material-icons v-show=qcollectionInbox.show>expand_less</i> <i class=material-icons v-else>expand_more</i> </div> </div> <div style=\"margin:16px 0;width:100%;height:16px\"> </div> <card> <div slot=content style=\"padding:8px 16px 0 16px\"> <div class=\"newquestion-difficulty-box flex-row flex-center\"> <span>難度：</span> <span class=flex-row> <i v-for=\"1 in 5\" class=material-icons @click=\"newQuestion.difficulty = $index + 1\" :class=\"{'difficulty-heighlight': newQuestion.difficulty > $index}\">star_rate</i> </span> </div> <div class=flex-column style=position:relative;top:-16px> <div v-show=\"newQuestion.tags.length !== 0\" style=\"padding-top: 25px;margin-right: 10px\"> <span>標籤：</span> <span class=q-tag @click=removeTag($index) v-for=\"tag in newQuestion.tags\" track-by=$index>{{tag}}</span> </div> <mdl-textfield label=輸入標籤.回車 @keyup.enter=addTag() :value.sync=tag style=width:200px></mdl-textfield> </div> </div> </card> <div class=flex-row> <div class=flex-50> <card> <div slot=content> <quill :content.sync=editorPreview.question></quill> </div> </card> </div> <div class=flex-50> <card> <div slot=content> <p class=toolbar-title>預覽</p> <div id=question-preview-container></div> </div> </card> </div> </div> <div class=mc-preview-wrapper id=mc-preview-container> <p style=margin:0;text-align:center;color:#9E9E9E>點擊選項設定正確答案</p> <div class=flex-row> <card flex=true class=flex-50 :class=\"{'hightlight-answer': newQuestion.answer.mc === 0}\" flex=true @click=\"newQuestion.answer.mc = 0\"> <div slot=content class=\"flex-row flex-baseline\"><span class=mc-label>A</span><div id=mc1></div></div> </card> <card flex=true class=flex-50 :class=\"{'hightlight-answer': newQuestion.answer.mc === 1}\" flex=true @click=\"newQuestion.answer.mc = 1\"> <div slot=content class=\"flex-row flex-baseline\"><span class=mc-label>B</span><div id=mc2></div></div> </card> </div> <div class=flex-row> <card flex=true class=flex-50 :class=\"{'hightlight-answer': newQuestion.answer.mc === 2}\" flex=true @click=\"newQuestion.answer.mc = 2\"> <div slot=content class=\"flex-row flex-baseline\"><span class=mc-label>C</span><div id=mc3></div></div> </card> <card flex=true class=flex-50 :class=\"{'hightlight-answer': newQuestion.answer.mc === 3}\" flex=true @click=\"newQuestion.answer.mc = 3\"> <div slot=content class=\"flex-row flex-baseline\"><span class=mc-label>D</span><div id=mc4></div></div> </card> </div> </div> <h5 style=margin-left:4px>編輯答案</h5> <div> <p style=margin:0;text-align:center;color:#9E9E9E>答案選項輸入</p> <div class=flex-row> <card class=flex-50><div slot=content><quill :content.sync=editorPreview.answer.mc[0]></quill></div></card> <card class=flex-50><div slot=content><quill :content.sync=editorPreview.answer.mc[1]></quill></div></card> </div> <div class=flex-row> <card class=flex-50><div slot=content><quill :content.sync=editorPreview.answer.mc[2]></quill></div></card> <card class=flex-50><div slot=content><quill :content.sync=editorPreview.answer.mc[3]></quill></div></card> </div> </div> <div class=flex-column style=margin-top:16px> <mdl-button primary raised @click=publishQuestion() v-bind:disabled=publishButton.disabled> 添加到題集 </mdl-button> </div> </div> </div>";


